
.btn {
  appearance: none;
  cursor: pointer;
  outline: 0;
  border: none;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: inherit;
  font-style: normal;
  font-weight: normal;
  transition: background-color 0.25s, color 0.25s, border 0.25s, box-shadow 0.25s;
  position: relative;
  user-select: none;
  background-color: transparent;

  &-primary {
    border-radius: 50px;
    width: 100%;
    background-color: $base;
    font-weight: bold;
    font-size: 14px;
    text-transform: uppercase;
    text-align: center;
    height: 63px;
    color: $white;
    position: relative;
    box-shadow: 0 3px #74306c;
    font-family: 'Montserrat', sans-serif;

    @include mq('large-small', 'max') {
      //font-size: 18px;
    }

    &:focus,
    &:active:focus {
      box-shadow: 0 0 0 0.2rem rgba($base, 0.25);
    }

    &:hover,
    &:focus {
      background-color: #74306c;

      //svg{
      //
      //    path{
      //        fill: $dark
      //    }
      //}
    }
  }

  &-secondary {
    border: 1px solid $base;
    //box-shadow: 0 8px 6px -8px #cfcfcf, 0 15px 51px -26px #adadad;
    background: $white;
    width: 100%;
    font-weight: 500;
    font-size: 12px;
    line-height: 21px;
    text-align: center;
    height: 40px;
    position: relative;
    padding: 0 20px;
    color: $base;
    border-radius: 50px;

    .ico{
      margin-left: 10px;
    }
    //svg {
    //    margin-left: 16px;
    //
    //    path {
    //        transition: fill 0.25s;
    //    }
    //}

    &:focus,
    &:active:focus {
        box-shadow: 0 0 0 0.2rem rgba($base, 0.25);
    }

    //&:hover, &:focus{
    //    //background: #c1c1c1;
    //    //color: $dark;
    //
    //    svg{
    //
    //        path{
    //
    //        }
    //    }
    //}
  }
}
