@font-face {
  font-family: "Gotham Pro";
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/GothamPro.eot?") format("eot"), url("../fonts/GothamPro.woff") format("woff"), url("../fonts/GothamPro.ttf") format("truetype");
}
@font-face {
  font-family: "Gotham Pro";
  font-style: normal;
  font-weight: bold;
  src: url("../fonts/GothamPro-Bold.eot?") format("eot"), url("../fonts/GothamPro-Bold.woff") format("woff"), url("../fonts/GothamPro-Bold.ttf") format("truetype");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
picture,
address {
  display: block;
}

body {
  line-height: 1;
}

img {
  max-width: 100%;
  height: auto;
}

ol,
ul {
  list-style: none;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

sup {
  vertical-align: super;
  font-size: 0.7rem;
  line-height: 1;
}

blockquote,
q {
  quotes: none;
}

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  text-decoration: none;
  color: inherit;
  -webkit-text-decoration-skip-ink: none;
  text-decoration-skip-ink: none;
  outline: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

select {
  background-image: url("data:image/svg+xml,%3Csvg width='201' height='174' viewBox='0 0 201 174' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M100.5 174L0.474075 0.749981L200.526 0.749999L100.5 174Z' fill='%23BF3CB0'/%3E%3C/svg%3E%0A");
