body {
  font-family: "Comfortaa", cursive;
  font-style: normal;
  font-weight: normal;
  color: #312e50;
  background: #fff;
  display: flex;
  flex-direction: column;
  direction: ltr;
  height: auto;
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-osx-font-smoothing: auto;
  -webkit-overflow-scrolling: touch;
  font-size: 14px;
  line-height: 1.2;
  overflow-x: hidden;
  padding: 0;
  margin: 0;
  width: 100%;
}

body.hidden {
  overflow: hidden;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

html.hidden {
  overflow: hidden;
}

