.form-control {
  box-shadow: none;
  border: 1px solid #d6d5e3;
  background-color: transparent;
  font-weight: 300;
  font-size: 14px;
  line-height: 25px;
  color: #696969;
  height: 64px;
  outline: 0;
  border-radius: 50px;
  transition: background 0.25s, color 0.25s, border-color 0.25s, box-shadow 0.15s;
  width: 100%;
  text-align: left;
  font-family: "Montserrat", sans-serif;
  padding: 0 22px;
}

.form-control::-webkit-input-placeholder {
  /* Chrome */
  color: #696969;
}

.form-control:-ms-input-placeholder {
  /* IE 10+ */
  color: #696969;
}

.form-control::-moz-placeholder {
  /* Firefox 19+ */
  color: #696969;
  opacity: 1;
}

.form-control:-moz-placeholder {
  /* Firefox 4 - 18 */
  color: #696969;
  opacity: 1;
}

.form-control:focus, .form-control:active:focus {
  box-shadow: 0 0 0 0.1rem rgba(191, 60, 176, 0.5);
}

.form-textarea {
  box-shadow: none !important;
  border: 2px solid transparent;
  background-color: #fff;
  font-weight: 300;
  font-size: 18px;
  line-height: 23px;
  color: #64626a;
  font-family: inherit;
  padding-left: 0;
  resize: none;
  outline: 0;
  width: 100%;
  padding-top: 15px;
  padding-bottom: 12px;
  border-radius: 0 !important;
  transition: border-color 0.15s ease-in;
}

.form-textarea::-webkit-input-placeholder {
  /* Chrome */
  color: #64626a;
}

.form-textarea:-ms-input-placeholder {
  /* IE 10+ */
  color: #64626a;
}

.form-textarea::-moz-placeholder {
  /* Firefox 19+ */
  color: #64626a;
  opacity: 1;
}

.form-textarea:-moz-placeholder {
  /* Firefox 4 - 18 */
  color: #64626a;
  opacity: 1;
}

.form-textarea:focus, .form-textarea:active {
  border-color: #bf3cb0;
}

